// ---------------------------------------------------------------
// Global wrapper (layout) for the SoilScout dashboard application
// ---------------------------------------------------------------
// • This file persists across every page under /app (Next.js App Router)
// • The Sidebar is always visible and never re-renders between pages
// • Each route/page will be rendered inside `{children}`
// • Shared UI elements like the topbar live here instead of per-page
// ---------------------------------------------------------------

import "./globals.css";          // Global styling (layout, spacing, components)
import Sidebar from "./components/Sidebar"; // Navigation & collapse logic lives here
import Topbar from "./components/Topbar";

// App-wide metadata — injected into <head> for SEO + browser tab title
// Note: Only valid in server components (like layouts)
export const metadata = {
  title: "SoilScout Dashboard",
  description: "Live dashboard layout",
};


/* =====================================================================
   RootLayout()
   ---------------------------------------------------------------------
   This component defines the global structure of the UI:

   Layout structure:
   ┌───────────────────────────────┐
   │ Sidebar (persistent)          │
   │                               │
   ├───────────────────────────────┤
   │ Topbar (global header)        │
   │ Page content → {children}     │
   └───────────────────────────────┘

   Notes for future developers:
   -------------------------------------
   • Add new navigation items in Sidebar.jsx, not here
   • Page content automatically appears inside {children}
   • Ideal place to attach auth wrappers or global providers later
===================================================================== */

export default function RootLayout({ children }) {
  return (
    <html lang="en">
      <body>

        <Sidebar />

        <div className="main-wrapper">
          
          {/* ⬇ independent component like Sidebar */}
          <Topbar />

          {children}
        </div>

      </body>
    </html>
  );
}

